<template>
  <div class="query-form">
    <gs-form
      ref="queryForm"
      :model="query"
      inline
    >
      <gs-form-item
        label="文件名称"
        prop="fileName"
      >
        <gs-input
          v-model="params.fileName"
          placeholder="请输入文件名称"
        />
      </gs-form-item>

      <gs-form-item
        label="修改时间"
        prop="modifyDate"
      >
        <gs-date-picker
          v-model="modifyDate"
          input-type="input"
          type="date"
        />
      </gs-form-item>

      <gs-form-item
        label="持有人"
        prop="owner"
      >
        <gs-input
          v-model="params.owner"
          placeholder="请输入持有人"
        />
      </gs-form-item>

      <gs-form-item>
        <gs-button
          type="primary"
          @click="handleSearch"
        >查询</gs-button>
      </gs-form-item>
    </gs-form>
  </div>
</template>

<script>
import moment from 'moment';

export default {
  props: {
    query: {
      type: Object,
      default() {
        return {};
      }
    }
  },

  data() {
    const modifyDate = this.query.modifyDate;

    return {
      params: {
        ...this.query
      },
      modifyDate: modifyDate ? moment(modifyDate) : null
    };
  },

  methods: {
    handleSearch() {
      const modifyDate = this.modifyDate;

      this.$emit('search', {
        ...this.params,
        modifyDate: modifyDate ? modifyDate.format('YYYY-MM-DD') : null
      });
    }
  }
};
</script>



