<template>
  <div class="file-management-page">
    <div class="content-header">
      <bread-crumb current-level="文件管理" />
    </div>
    <div class="content-body">
      <action-group
        :file-path="filePath"
        @get-file-list="queryFileList"
        @upload-success="queryFileList(filePath)"
      />
      <search-form
        :query="formData"
        @search="handleSearch"
      />
      <data-table
        :loading="loading"
        :table-props="tableProps"
        :pagination-props="paginationProps"
        @sort-change="handleSortChange"
        @page-change="handlePageChange"
        @page-size-change="handlePageSizeChange"
      >
        <el-table-column
          prop="fileName"
          label="名称"
          sortable
        >
          <template slot-scope="scope">
            <a
              v-if="scope.row.type === 'Directory'"
              title="点击打开"
              @click="handleFileClick(scope)"
            >
              <gs-icon name=" iconfont icon-wenjianjia1" />{{ scope.row.fileName }}
            </a>
            <span
              v-else
              class="file-name"
              title="点击下载"
              @click="handleFileClick(scope)"
            >
              <gs-icon name=" iconfont icon-file" />{{ scope.row.fileName }}
            </span>
          </template>
        </el-table-column>
        <el-table-column
          prop="size"
          label="大小"
        >
          <template slot-scope="scope">
            {{ scope.row | formatSize }}
          </template>
        </el-table-column>
        <el-table-column
          prop="modificationTime"
          label="最后修改时间"
          width="180"
        />
        <el-table-column
          prop="owner"
          label="持有人"
          align="right"
        />
      </data-table>
    </div>
  </div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import DataTable from 'src/components/data-table';
import ActionGroup from './action-group';
import SearchForm from './search-form';
import {
  checkLoading
} from 'src/store/status';
import BreadCrumb from 'src/components/breadcrumb';
import './style';

export default {
  components: {
    ActionGroup,
    SearchForm,
    DataTable,
    BreadCrumb
  },

  filters: {
    formatSize(row) {
      return row.type === 'Directory' ? '-' : row.size;
    }
  },

  data() {
    return {
      loading: false,
      searchMode: false,
      filePath: '/',
      formData: {},
      page: 1,
      pageSize: 15,
      orderBy: 'fileName',
      sortType: 'descending'
    };
  },

  computed: {
    ...mapState('file', {
      list: 'list'
    }),
    tableProps() {
      return {
        defaultSort: {
          prop: this.orderBy,
          order: this.sortType
        },
        data: this.list.data
      };
    },
    paginationProps() {
      return {
        current: this.page,
        pageSize: this.pageSize,
        total: this.list.total
      };
    },
    commonParams() {
      const {
        orderBy,
        sortType,
        page,
        pageSize
      } = this;
      const sortMap = {
        ascending: 'asc',
        descending: 'desc'
      };

      return {
        orderBy: orderBy,
        sortType: sortMap[sortType],
        offset: (page - 1) * pageSize,
        limit: pageSize
      };
    }
  },

  watch: {
    'list.status': function (val) {
      this.loading = checkLoading(val);
    }
  },

  mounted() {
    this.queryFileList(this.filePath);
  },

  methods: {
    ...mapActions('file', [
      'getFileList',
      'searchFileList',
      'downloadFile'
    ]),

    queryFileList(filePath) {
      this.filePath = filePath;
      if (this.searchMode) {
        this.searchMode = false;
        this.page = 1;
      }
      this.getFileList({
        filePath,
        ...this.commonParams
      });
    },

    findFileList() {
      this.searchFileList({
        ...this.formData,
        ...this.commonParams
      });
    },

    handleSearch(params) {
      this.filePath = '/';
      this.formData = params;
      this.page = 1;

      if (params.fileName || params.modifyDate || params.owner) {
        this.searchMode = true;
        this.findFileList();
      } else {
        this.searchMode = false;
        this.queryFileList(this.filePath);
      }
    },

    queryOrFindFileList() {
      if (this.searchMode) {
        this.findFileList();
      } else {
        this.queryFileList(this.filePath);
      }
    },

    handleSortChange({ order }) {
      if (this.sortType !== order) {
        this.sortType = order;
        this.queryOrFindFileList();
      }
    },

    handlePageChange(page) {
      this.page = page;
      this.queryOrFindFileList();
    },

    handlePageSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.queryOrFindFileList();
    },

    handleFileClick({ row }) {
      const filePath = row.filePath;

      if (row.type === 'Directory') {
        this.page = 1;
        this.queryFileList(filePath);
      } else {
        this.downloadFile(filePath);
      }
    }
  }
};
</script>




