<template>
  <div class="action-group">
    <gs-button
      v-if="paths.length > 1"
      @click="jumpPath(paths.length - 2)"
    >返回上一级</gs-button>
    <gs-button
      type="primary"
      @click="open"
    >上传文件</gs-button>
    <span class="file-path-text">{{ filePath }}</span>

    <gs-modal
      v-model="visible"
      class="file-management-upload-modal"
      title="上传文件"
    >
      <gs-row>
        <gs-col
          :span="labelSpan"
          class="upload-label"
        >选择文件：</gs-col>
        <gs-col :span="cntSpan">
          <gs-upload :before-upload="beforeUpload">
            <gs-button type="primary">选择文件</gs-button>
          </gs-upload>
          <ul class="upload-files">
            <li
              v-for="(item, i) in fileList"
              :key="i"
              class="upload-file-item"
            >
              {{ item.name }}
              <gs-icon
                v-if="!loading"
                name="close"
                type="primary"
                mode="button"
                @click="deleteFile(i)"
              />
            </li>
          </ul>
        </gs-col>
      </gs-row>
      <gs-row>
        <gs-col
          :span="labelSpan"
          class="path-label"
        >位置：</gs-col>
        <gs-col :span="cntSpan">
          <span>{{ filePath || '/' }}</span>
        </gs-col>
      </gs-row>
      <template slot="footer">
        <gs-button
          :loading="loading"
          type="primary"
          @click="handleUpload"
        >确定</gs-button>
        <gs-button
          :disabled="loading"
          @click="close"
        >取消</gs-button>
      </template>
    </gs-modal>
  </div>
</template>

<script>
import {
  mapActions
} from 'vuex';

export default {
  props: {
    filePath: {
      type: String,
      default: '/'
    }
  },

  data() {
    return {
      loading: false,
      fileList: [],
      visible: false,
      labelSpan: 5,
      cntSpan: 19
    };
  },

  computed: {
    paths() {
      const paths = this.filePath.split('/').filter(path => !!path);

      paths.unshift('/');
      return paths;
    }
  },

  watch: {
    visible(val) {
      if (!val) {
        this.fileList = [];
      }
    }
  },

  methods: {
    ...mapActions('file', [
      'uploadFile'
    ]),

    open() {
      this.visible = true;
    },

    close() {
      this.visible = false;
    },

    handleUpload() {
      const {
        filePath,
        fileList
      } = this;

      if (fileList.length === 0) {
        this.$Message.warning('请选择要上传的文件！', 1.5);
        return false;
      }

      const file = fileList[0].origin;
      this.loading = true;
      this.uploadFile({
        filePath: `${filePath}/${file.name}`.replace(/\/\//g, '/'),
        file
      }).then(() => {
        this.loading = false;
        this.visible = false;
        this.fileList = [];
        this.$emit('upload-success');
        this.$Message.success('上传成功', 1.5);
      }, () => {
        this.loading = false;
      });
    },

    deleteFile(index) {
      this.fileList.splice(index, 1);
    },

    beforeUpload(file) {
      file.status = 'waiting';
      this.fileList = [file];
      return false;
    },

    jumpPath(index) {
      let paths = this.paths;
      let filePath = paths[0];

      for (let i = 1; i <= index; i++) {
        filePath += `/${paths[i]}`;
      }

      this.$emit('get-file-list', filePath.replace(/\/\//g, '/'));
    }
  }
};
</script>




